.signup {
    width: 100%;
    height: 100px;
    margin-top: 20px;
    padding: 0px 150px;
    margin-bottom: 50px;
}

.signup_container {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.signup_left > p {
    font-family: sans-serif;
    font-size: 32px;
    color: #5c6874;
    padding-left: 70px;
}

.signup_right {
    display: flex;
    align-items: center;
}

.signup_right > button {
    padding: 18px;
    background-color: #1b88f4;
    color: white;
    outline: none;
    border: none;
    font-size: 16px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    cursor: pointer;
}
