.card {
    width: 100%;
    padding: 0px 150px;
    height: 350px;
    margin-top: 20px;
    position: relative;
    overflow: hidden;
}

.card_container {
    width: 100%;
    height: 100%;
    border-radius: 20px;
    display: grid;
    grid-template-columns: 300px auto 300px;
    background-color: white;
    padding: 20px;
    position: relative;
}

.circle {
    position: absolute;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: white;
    left: -30px;
    top: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid rgba(128, 128, 128, 0.222);
}

.card_left {
    display: flex;
    align-items: center;
    padding: 15px;
}

.card_left > img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.card_middle {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    padding: 20px;
}

.card_middle > p {
    font-family: sans-serif;
    font-family: 16px;
    line-height: 25px;
    padding: 15px 0px;
}

.card_middle > span {
    display: flex;
    align-items: center;
    cursor: pointer;
    color: blue;
    font-size: 16px;
    font-family: sans-serif;
    margin-top: 20px;
}

.card_right {
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: center;
}

.card_top {
    position: absolute;
    width: 300px;
    height: 200px;
    background-color: #f3f9ff;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 20px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    top: -10px;
    right: 0;
}

.card_top > p {
    font-family: sans-serif;
    font-size: 50px;
}

.card_top > h4 {
    font-family: sans-serif;
    font-size: 20px;
    margin-top: 20px;
}

.icon {
    display: flex;
    align-items: center;
    margin-top: 10px;
}

.card_right > button {
    font-size: 18px;
    font-family: sans-serif;
    border: none;
    outline: none;
    width: 100%;
    padding: 15px 0px;
    border-radius: 25px;
    color: white;
    background-color: #1b88f4;
    cursor: pointer;
}
