.footer {
    width: 100%;
    height: 452px;
    background-color: #212731;
    display: flex;
    align-items: flex-start;
    /* justify-content: center; */
    color: white;
    padding: 80px 280px;
}

.footer_left {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    width: 350px;
}

.footer_left > h1 {
    font-family: sans-serif;
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 22px;
}

.footer_left > span {
    font-family: sans-serif;
    font-size: 13px;
    color: gray;
    margin-bottom: 22px;
}

.footer_middle {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    width: 350px;
}

.footer_middle > h1 {
    font-family: sans-serif;
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 22px;
}

.footer_middle > span {
    font-family: sans-serif;
    font-size: 13px;
    color: gray;
    margin-bottom: 22px;
}

.footer_right {
    display: flex;
    align-items: flex-start;
    width: 350px;
}

.footer_right > select {
    margin-top: 50px;
    border: none;
    outline: none;
    background-color: transparent;
    color: gray;
}
