.header2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    // background-color: white;
}

.header2_top {
    display: flex;
    align-items: center;
    padding: 25px 0px;
    p {
        font-size: 80px;
        font-family: sans-serif;
    }
}

.header2_bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid rgba(128, 128, 128, 0.329);
    border-bottom: 1px solid rgba(128, 128, 128, 0.329);
    height: 45px;
    width: 100%;
    padding: 0px 150px;
}

.header2_left {
    display: flex;
    align-items: center;
    gap: 30px;
}

.left_container {
    display: flex;
    align-items: center;
}

.left_container > p {
    font-family: sans-serif;
    font-size: 16px;
    margin-left: 10px;
    color: gray;
}

.header2_right {
    display: flex;
    align-items: center;
    font-family: sans-serif;
    font-size: 16px;
}

.header2_right > p {
    margin-right: 10px;
    color: gray;
}
