.header {
    height: 70px;
    width: 100%;
    background-color: #202731;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 150px;
}

.input {
    display: flex;
    align-items: center;
    width: 400px;
    background-color: white;
    border-radius: 10px;
    height: 38px;
    overflow: hidden;
    padding: 0px 10px;
}

.input > input {
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    font-size: 16px;
    padding: 0px 10px;
}

.right {
    display: flex;
    align-items: center;
    gap: 100px;
    color: white;
}

.right > span {
    cursor: pointer;
    font-family: sans-serif;
}
