.boxes {
    margin-top: 50px;
    padding: 0px 150px;
    width: 100%;
    overflow: hidden;
}

.box_container {
    // background-color: white;
    width: 100%;
    border-radius: 10px;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.box_top {
    width: 100%;
    display: flex;
    align-items: flex-start;
    padding: 20px;
}

.box_top > p {
    font-size: 30px;
    font-family: sans-serif;
}

.box_bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 60px;
    padding-bottom: 20px;
    width: 100%;
}

.box_card {
    width: 350px;
    height: 420px;
    background-color: white;
    border-radius: 15px;
}

.box_card {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.boxcard_top {
    width: 100%;
    height: 50%;
    padding: 15px;
}

.boxcard_top > img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.boxcard_bottom {
    display: flex;
    // align-items: center;
    flex-direction: column;
    width: 100%;
    padding: 10px 15px;
}

.boxcard_bottom > .discount {
    display: flex;
    align-items: flex-start;
    gap: 10px;
}

.discount > span {
    font-size: 15px;
    font-family: sans-serif;
    padding: 8px;
    background-color: whitesmoke;
    border-radius: 10px;
}

.boxcard_bottom > h5 {
    width: 100%;
    text-align: center;
    margin: 8px 0px;
    font-family: sans-serif;
    font-weight: 600;
    font-size: 16px;
}

.boxcard_bottom > p {
    font-size: 17px;
    font-family: sans-serif;
    letter-spacing: 1px;
}

.prize {
    width: 100%;
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 8px;
}

.prize > h2 {
    font-family: sans-serif;
    font-size: 17px;
    font-weight: 500;
}

.prize > h4 {
    font-family: sans-serif;
    font-size: 14px;
    font-weight: 500;
    margin-top: 8px;
    color: gray;
}
.prize > p {
    font-family: sans-serif;
    font-size: 14px;
    font-weight: 500;
    margin-top: 8px;
    color: red;
}

.boxcard_bottom > button {
    color: white;
    background-color: #1b88f4;
    border: none;
    outline: none;
    font-family: sans-serif;
    font-size: 16px;
    padding: 10px 0px;
    border-radius: 12px;
    margin-top: 10px;
}
