.optionbar {
    width: 100%;
    /* background-color: white; */
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px 150px;
}

.optionbar_top {
    display: flex;
    align-items: center;
    gap: 20px;
    width: 100%;
    padding: 18px 0px;
}

.optionbar_top > span {
    background-color: white;
    padding: 15px 40px;
    border-radius: 15px;
    cursor: pointer;
    color: gray;
}

.optionbar_bottom {
    display: flex;
    align-items: center;
    width: 100%;
    font-size: 16px;
    font-family: sans-serif;
    color: gray;
    padding: 10px 0px;
}

.optionbar_bottom > p {
    margin-left: 20px;
}
